import { Component, Vue } from 'vue-property-decorator'
import {
  getContractTemplateList,
  deleteContractTemplate,
} from '@/api/contract/contractApi'
@Component({
  components: {},
})
export default class ContractTemplate extends Vue {
  private pageTitle = '合同模板'
  // 数据源
  private dataSource: any[] = []
  // 分页数据
  private pagination = {
    total: 0,
    current: 1,
    size: 10,
  }
  /**
   * @Author roct
   * @Description 修改当前页
   * @Date 20:08 2021/5/19 0019
   **/
  private handleCurrentChange(e: any) {
    this.pagination.current = e
    this.getContractTemplateList()
  }
  // 获取合同模板列表
  private async getContractTemplateList() {
    const res = await getContractTemplateList(this.pagination)
    if (res.code === 200) {
      this.pagination.total = res.data.total
      this.dataSource = res.data.records
    }
  }
  // 删除某个合同
  private async deleteTemplate(scope: any) {
    const res = await deleteContractTemplate(scope.row.id)
    if (res.code === 200) {
      await this.getContractTemplateList()
    }
  }
  public mounted(): void {
    this.getContractTemplateList()
  }
}
